<template>
	<section  :class="scaleClass">
		<template v-if="showRouterView">
			<router-view ></router-view>
			<!--<child :spid="parentMsg" style="visibility: hidden;;"></child>-->
		</template>
		<template v-else>
			<div class="toolbar"> 
				<el-form :model="filters" :inline="true" >
					<el-form-item>
						<!--<el-input placeholder="品名" v-model="filters.name" size="medium" clearable></el-input>-->
						<el-input placeholder="款号/品名/采购编码" v-model="filters.name" class="input-with-select" clearable size="medium" style="position: relative;top: -2px;">
						    <el-select v-model="filters.spxxPageForm" slot="prepend" placeholder="类型" >
						      <el-option label="款号" value="spcode"></el-option>
						      <el-option label="品名" value="spmc"></el-option>
						      <el-option label="采购编码" value="itemno"></el-option>
						    </el-select>
						    <el-button slot="append" icon="el-icon-search" @click.native.prevent="search"></el-button>
					    </el-input>
					</el-form-item>
					<!--<el-form-item>
						<el-button type="primary" @click.native.prevent="search" size="medium" class="fa fa-search">查询</el-button>
					</el-form-item>-->
					<el-form-item>
						<el-button type="primary" size="medium" @click.native.prevent="MerchandiseTableAdd"  class="fa fa-plus">新增</el-button>
					</el-form-item>
					<el-form-item>
						<el-button type="primary" size="medium" @click.native.prevent="pwdProductInfo" class="fa fa-pencil">修改</el-button>
					</el-form-item>
					<el-form-item>
						<el-button type="primary" size="medium" @click.native.prevent="uploadImage" class="fa fa-upload">上传图片</el-button>
					</el-form-item>  
					<el-form-item>
						<el-button type="primary" size="medium" @click.native.prevent="audit" class="fa fa-paper-plane">提交审核</el-button>
					</el-form-item>
					<!--<el-form-item>
						<el-button type="primary" size="medium" @click.native.prevent="deleted" class="fa fa-trash-o">删除</el-button>
					</el-form-item>-->
				</el-form>
			</div>				
			<el-table 
				ref="multipleTable"
				:data="tableData" 
				stripe   
				border			
				style="width:100%;" 
				:highlight-current-row=true
				:show-header=true
				:default-sort = "{prop: 'name', order: 'descending'}"
				class="el-table"
				:class="bordershadow"
		 		v-loading="loading"
		 		@clearSelection="clearSelect"
		 		@selection-change="selectionchange"
		 		@row-click="clickRow"
		 		:row-class-name="tableRowClassName"
		 		:header-cell-style="{background:'#eef1f6'}"
		 		:cell-style="cellStyle"
				>
			  <el-table-column align="center" width="55">
	              <template slot-scope="scope">
	                <el-radio @change.native="getCurrentRow(scope.$index)" :label="scope.$index" v-model="radio" class="textRadio">&nbsp;</el-radio>
	              </template>
              </el-table-column>
		      <el-table-column
		        prop="spmc"
		        label="品名"
		        min-width="200"
		        sortable
		        >
		      </el-table-column>
		      <el-table-column
		        prop="dw"
		        label="单位"
		        sortable
		        align="center"
		        min-width="80"
		        >
		      </el-table-column>
		      <el-table-column
		        prop="cz"
		        label="材质"
		        sortable
		        align="center"
		        min-width="80"
		        >
		      </el-table-column>
		      <el-table-column
		        prop="spcode"
		        label="款号"
		        sortable
		        align="center"
		        min-width="90"
		        >
		      </el-table-column>
		      <el-table-column
		        prop="itemno"
		        label="采购编码"
		        sortable
		        align="center"
		        min-width="110"
		        >
		      </el-table-column>
		      <el-table-column
		        prop="createtime"
		        label="创建时间"
		        min-width="140"
		        sortable
		        align="center"
		        >
		      </el-table-column>
		      <el-table-column
		        prop="zzscSpxxGysOptVo.auditstatus"
		        label="审核状态"
		        sortable
		        :filters="[{text: '默认', value: 0}, {text: '待审核', value: 1}, {text: '审核通过', value: 5}, {text: '审核不通过', value: 9}]"
      			:filter-method="filterauditstatus"
      			align="center"
		        min-width="120"
		        >
		        <template slot-scope="scope">
			    	<el-row v-if="scope.row.zzscSpxxGysOptVo.auditstatus==0">
			    		<span>默认</span>
					</el-row>
					<el-row v-if="scope.row.zzscSpxxGysOptVo.auditstatus==1">
						<!--<i class="el-icon-question"></i>-->
						<!--<i class="fa fa fa-user" aria-hidden="true"></i>-->
			    		<span>待审核</span>
					</el-row>
					<el-row v-if="scope.row.zzscSpxxGysOptVo.auditstatus==5">
						<!--<i class="el-icon-check"></i>-->
						<!--<i class="fa fa-check" aria-hidden="true"></i>-->
			    		<span>审核通过</span>
					</el-row>
					<el-row v-if="scope.row.zzscSpxxGysOptVo.auditstatus==9">
						<!--<i class="el-icon-close"></i>-->
			    		<span>审核不通过</span>
					</el-row>
			    </template>
		      </el-table-column>
		      <el-table-column
		        prop="costprice"
		        label="采购价"
		        sortable
		        align="center"
		        min-width="90"
		        >
		      </el-table-column>
		      <el-table-column
		        prop="zzscSpxxGysOptVo.priceaudittime"
		        label="价格审核时间"
		        sortable
		        align="center"
		        min-width="140"
		        >
		      </el-table-column>
		      <el-table-column
		        prop="zzscSpxxGysOptVo.priceauditbz"
		        label="价格审核备注"
		        sortable
		        align="center"
		        min-width="140"
		        >
		      </el-table-column>
		      <el-table-column
		        prop="zzscSpxxGysOptVo.infoaudittime"
		        label="资料审核时间"
		        min-width="140"
		        sortable
		        align="center"
		        >
		      </el-table-column>
		      <el-table-column
		        prop="zzscSpxxGysOptVo.infoauditbz"
		        label="资料审核备注"
		        width="140"
		        sortable
		        align="center"
		        >
		      </el-table-column>
		      <!--<el-table-column
		        prop="submitbz"
		        label="备注"
		        sortable
		        align="center"
		        min-width="150"
		        >
		      </el-table-column>-->
		      <el-table-column
		        prop="hasimage"
		        label="图片"
		        sortable
		        :filters="[{text: '有', value: true}, {text: '无', value: false}]"
      			:filter-method="filterhasimage"
      			align="center"
		        min-width="100"
			    >
			    <template slot-scope="scope">
			    	<el-row v-if="scope.row.hasimage">
			    		<i class="fa fa-photo" aria-hidden="true" style="cursor: pointer;" title="浏览图片" @click="seeImage(scope.row.id)"></i>
			    		<span></span>
			    		<!--<i class="fa fa-eye" aria-hidden="true" ></i>-->
						<!--<i class="el-icon-view" ></i>-->
					</el-row>
					<el-row v-else>
			    		<span>无</span>
					</el-row>
			    </template>
		      </el-table-column>
		      <el-table-column
		        prop="zzscSpxxGysOptVo.submitaudittime"
		        label="当前提交审核时间"
		        min-width="160"
		        sortable
		        align="center"
		        >
		      </el-table-column>
		     <!-- <el-table-column
			      label="操作"
			      width="300">
			      <template slot-scope="scope">
			      	<el-row>
			      		<el-button type="primary" size="small" class="fa fa-upload"  @click.native.prevent="uploadImage(scope.row.id)">上传图片</el-button>
				        <el-button type="success" size="small" class="fa fa-pencil" @click.native.prevent="pwdProductInfo(scope.row)">修改</el-button>
				        <el-button type="danger"  size="small"  @click="deleted" class="fa fa-trash-o">删除</el-button>
			      	</el-row>
			      </template>
			  </el-table-column>-->
			</el-table>
			<div class="pager">
				<!--<template v-if="disabled">
					<el-button disabled class="fa fa-trash-o">批量删除</el-button>
				</template>
				<template v-else>
					<el-button type="danger" class="fa fa-trash-o">批量删除</el-button>
				</template>-->
				<el-pagination background layout="prev, pager, next" :total="total" style="float:right;"  
    				@current-change="handleCurrentChange">
				</el-pagination>
			</div>
			<el-dialog
			  title="上传图片"
			  :visible.sync="dialogVisible"
			  width="30%"
			  >
			  	<el-upload
				  :action="uploadUrl()"
				  list-type="picture-card"
				  multiple
          		  accept="image/png, image/jpeg"
				  :before-upload="beforeUpload"
		          :on-remove="handleRemove"
		          :on-success="uploadSuccess"
		          :on-error="uploadError"
				  :show-file-list="true"
				  enctype="multipart/form-data"
				  ref="upload"
				  :headers="myHeaders">
				  <i class="el-icon-plus"></i>
				</el-upload>
				<!--<el-dialog :visible.sync="imgVisible">
				  <img width="100%" :src="dialogImageUrl" alt="">
				</el-dialog>-->
				<span slot="footer" class="dialog-footer">
				    <el-button type="primary" @click.native.prevent="handleFinshUpload">完成</el-button>
				</span>
			</el-dialog>
			<el-dialog
			  title="图片墙"
			  :visible.sync="dialogVisibleSee"
			  width="570px">
			  	<el-row>
				  <el-col :span="8" v-for="(img,index) in cardImgs" >
				    <el-card :body-style="{ padding: '0px',position:'relative' }" class="cardImgList">
				      <img :src="img.path" width="100%" style="height: 180px;">
				      <i class="el-icon-zoom-in" style="cursor: pointer;" @click="zoomInImage(img.path)"></i>
				      <i class="el-icon-delete" style="cursor: pointer;" @click="deleteImage(img.id,index)"></i>
				      <div class="fixgroundcolor"></div>
				    </el-card>
				    <el-dialog :visible.sync="dialogVisibleCard">
					  <img width="100%" :src="dialogImageUrlCard" alt="">
					</el-dialog>
				  </el-col>
				</el-row>
				<span slot="footer" class="dialog-footer">
				    <el-button type="primary" @click.native.prevent="handleFinshSee">关闭</el-button>
				</span>
			</el-dialog>
		</template>
	</section>
</template>

<script>
  import sptb from '../../../assets/js/nav/sptb/sptb.js'
  export default sptb
</script>
<style scoped lang="scss" >
@import '../../../assets/css/nav/sptb/sptb.scss'
</style>

<style>
.el-table td, .el-table th{
	padding: 6px 0!important;
}
.el-pagination .btn-next, .el-pagination .btn-prev{
	margin: 0!important;
    border: 1px solid #d1dbe5;
    background-color:#fff!important;
    border-radius: 0!important; 
}
.el-pagination.is-background .el-pager li:not(.disabled).active{
	border: 1px solid #409EFF;
}
.el-pagination.is-background .el-pager li {
    margin: 0 1px!important;
    border: 1px solid #d1dbe5;
    box-sizing: border-box;
    background-color:#fff;
    border-radius: 0;
}
.el-table .cell{
    white-space: nowrap!important;
}
.v-modal{
	z-index: 2000!important;
}
 .el-select .el-input {
    width: 110px;
  }
  .input-with-select .el-input-group__prepend {
    background-color: #fff;
  }
</style>


