        bindWalletBankCard(){
        	this.$refs.ruleCard.validate((valid)=>{
				if(valid){
					this.$api.put('zzscBankCardBind/bindWalletBankCard/'+this.ruleCard.card,{},localStorage.token, success => {  
		      	 	this.getCardList();
		 			this.$message({
			            type: 'success',
			            message: success.msg
			          });
			          this.dialogBandCardVisible=false;
				  },
				 failure=>{
				   this.$message({
			            type: 'error',
			            message: failure.msg
			      	});
				 })
				}
				})
			
        },
        //获取已绑银行卡
