//import child from './children/MerchandiseTableUploadImg'  //引入child组件
export default{
      data() {
        return {
          filters: {name: '',spcode:'',spmc:'',itemno:'',spxxPageForm:''},
				  currentPage:1,
        	pagesize:10,
				  disabled:true,
				  total:0,
				  loading: true,
        	tableData: [],
          showRouterView:false,
          scaleClass:'',
          dialogVisible: false,
          dialogVisibleSee:false,
          dialogImageUrl: '',
          imgVisible: false,
          spid:"",
          bordershadow:'',
          selectItem:0,
          spidList:[],
          spidListNot:[],
          spidNot:"",
          myHeaders: '',
          submitbz:'',
          spmc:[],
          cardImgs:[],
          dialogVisibleCard:false,
          dialogImageUrlCard:'',
          radio:'',
          row:[]
        }
      },
      components: {
           // child
      },
      created() {
  	 		this.getBread();//刷新，路由内容处理
		 		this.getErpSpxxPageList();
		 		this.myHeaders={Authorization:'Bearer '+localStorage.token};
      },
      watch: {
            $route() {
                this.getBread();//监听路由变化，隐藏二级路由的内容
                this.getErpSpxxPageList();//子页面返回刷新当前页面
            }
      },
      beforeMount(){

      },
      mounted()  {
				this.scaleClass="scaleClass"
      },
	    beforeUpdate() {
	     	 	 
	    },
	    updated(){
	     	 
	    },
      methods:{
	      formatDate(row, column, cellValue){
          let newDate = row.createtime== "" ? "-" : row.createtime.slice(0,10)+" "+row.createtime.slice(11,19)
          return newDate
	      },
				cellStyle(data){
					if(data.columnIndex == 7&& data.row.zzscSpxxGysOptVo.auditstatus==0){
						 return 'background:#cccccc;color:#ffffff;'
					}else if(data.columnIndex == 7&& data.row.zzscSpxxGysOptVo.auditstatus==1){
						return 'background:#409EFF;color:#ffffff;'
					}else if(data.columnIndex == 7&& data.row.zzscSpxxGysOptVo.auditstatus==5){
						return 'background:#67c23a;color:#ffffff;'
					}else if(data.columnIndex == 7&& data.row.zzscSpxxGysOptVo.auditstatus==9){
						return 'background:#f56c6c;color:#ffffff;'
					}
				},
				handleCurrentChange(){},
        getBread(){
					let matched=this.$route.matched.filter(item=>item.name)//$route.matched 将会是一个包含从上到下的所有对象 (副本)。
					if(matched.length>=3){
						this.showRouterView=true;
					}else{
						this.showRouterView=false;
					}
				},
      	//查询
      	search(){
      		this.loading=true;
      		this.getErpSpxxPageList();
      	},
      	MerchandiseTableAdd(){     		
      		this.$router.push({ path: '/nav/sptb/child/add' }); 
      	},
      	//审核
      	audit(){
      		if(this.spid!=""){
      			let flag=true;
      			for(let i=0;i<this.tableData.length;i++){
      				if(this.tableData[i].id==this.spid){
      					if(this.tableData[i].hasimage){
      						flag=false;
      					}
      				}
      			}
      			if(flag){
      				this.$message({
				          message: '请先上传图片',
				          type: 'warning'
			         });	
      			}else{
			       this.$prompt('填写备注内容（非必填）', '提示', {
					          confirmButtonText: '去审核',
					          showCancelButton:'取消',
					        }).then(({ value }) => {
					          this.submitbz=value;
            				this.$api.post('product/submitProductInfo/'+this.spid,  
				      			{'submitbz':this.submitbz}
				      		,localStorage.token, success => {
					         	this.total=success.total;
							      this.tableData=success.list;
							      this.getErpSpxxPageList();
							      this.$message({
									          message: success.msg,
									          type: 'success'
							         });	
									  },
									 failure=>{
									    this.loading = false;
									    if(failure==401){
								         this.$message({
									          message: '授权失败，请重新登录',
									          type: 'error'
								         });	
								        this.$router.push({path:'/'})
									    }
									    console.log(typeof this.submitbz);
									    this.$message({
									          message: failure.msg,
									          type: 'error'
							         });	
									 })
					        })
      			}
      		}else if(this.spidNot!=""){
      			this.$message({
				          message: '当前数据不需要审核',
				          duration:'1500',
				          type:'warning'
			         	});
      		}else{
      			this.bordershadow="colorRed"
      			setTimeout(()=>{
      					this.bordershadow=""
      			},2000)
		      		this.$message({
				          message: '请选择一行',
				          duration:'2000',
				          type:'warning'
			        });
      		}
      	},
      	//选中指定行背景颜色
      	tableRowClassName(data){     				
      		for(let i=0;i<this.spidListNot.length;i++){
      			if(data.row.id==this.spidListNot[i]&&data.row.auditstatus!=0){
      				return "eee"
	      		}
      		}
      	},
      	deleted(){
      		if(this.spid!=""){
      			this.$confirm("是否删除？","提示",{
	      			confirmButtonText:'ok',
	      			cancelButtonText:'Cancel',
	      			type:'warning'
	      		}).then(()=>{
	      			
	      		}).catch(()=>{
	      			
	      		})
      		} else if(this.spidNot!=""){
      			this.$confirm("是否删除？","提示",{
	      			confirmButtonText:'ok',
	      			cancelButtonText:'Cancel',
	      			type:'warning'
	      		}).then(()=>{
	      			
	      		}).catch(()=>{
	      			
	      		})
      		}else{
      			this.bordershadow="colorRed"
      			setTimeout(()=>{
      					this.bordershadow=""
      			},2000)
      			this.$message({
				          message: '请选择一行',
				          duration:'2000',
				          type:'warning'
			         });
      		}
		 			
      	},
      	clearAllChoose(){
      		 this.$refs.multipleTable.clearSelection();
      	},
        filterHandler(value, row, column) {
	        const property = column['country'];
	        return row[property] === value;
      	},
      	clearSelect(){},
      	getCurrentRow(data){
      		//console.log(data);
      	},
      	//复选框
      	selectionchange(data){
      		if(data.length>0){
      			this.selectItem=data.length;
      			this.disabled=false;
      			this.spidList.length=0;
      			this.spidListNot.length=0;
      			for(let i=0;i<data.length;i++){
      				if(data[i].auditstatus==0){
      					//this.spidList.push(data[i].id)
      					this.spidList=data[i].id
      				}else{
      					//this.spidListNot.push(data[i].id)
      					this.spidListNot=data[i].id
      				}
      			}
      		}else{
      			this.spidList.length=0;
      			this.spidListNot.length=0;
      			this.disabled=true;
      			this.selectItem=0;
      		}
      	},
  	 		clickRow(row){
  	 			this.row=row;
  	 			this.spid=""
  	 			this.spidNot=""
  	 			if(row.zzscSpxxGysOptVo.auditstatus==0){
  	 				this.spid=row.id
  	 			}else{
  	 				this.spidNot=row.id
  	 			}
  	 			
        	//this.$refs.multipleTable.toggleRowSelection(row)
        },  
      	getErpSpxxPageList(){
      		this.filters.spcode=""
      		this.filters.spmc=""
      		this.filters.itemno=""
    				if(this.filters.spxxPageForm=="spcode"){
    					this.filters.spcode=this.filters.name;
    				}else if(this.filters.spxxPageForm=="spmc"){
    					this.filters.spmc=this.filters.name;
    				}else if(this.filters.spxxPageForm=="itemno"){
    					this.filters.itemno=this.filters.name;
    				}
	  			this.$api.page('product/getErpSpxxPageList',  {
      			"page":this.currentPage,
      			"pageSize":this.pagesize,
      			"spcode":this.filters.spcode,
      			"spmc":this.filters.spmc,
      			"itemno":this.filters.itemno
      		},localStorage.token, success => {
	         	this.total=success.total;
			      this.tableData=success.list;
			       this.loading=false;
					  },
					 failure=>{
					   this.loading = false;
					   if(failure==401){
				       this.$message({
					          message: '授权失败，请重新登录',
					          type: 'error'
				         });	
				        this.$router.push({path:'/'})
					   }
					 })
      	},
      	//上传图片
      	uploadImage(){
					if(this.spid!=""){
      			this.dialogVisible=true;
	      		this.$refs.upload.clearFiles()
      		}else if(this.spidNot!=""){
      			this.$message({
				          message: '当前状态不能上传图片',
				          duration:'2000',
				          type:'error'
			         	});
      		}else{
      			this.bordershadow="colorRed"
      			setTimeout(()=>{
      					this.bordershadow=""
      			},2000)
      			this.$message({
				          message: '请选择一行',
				          duration:'2000',
				          type:'warning'
			         	});
      		}
      		
      	},
      	pwdProductInfo(){
      		if(this.spid!=""){
      			this.$router.push({ path: '/nav/sptb/child/update',query:{row:this.row} }); 
      		}else if(this.spidNot!=""){
      			//this.$router.push({ path: '/nav/sptb/child/update',query:{row:this.row} }); 
      			this.$message({
				          message: '当前状态不可以修改',
				          duration:'2000',
				          type:'error'
			         	});
      		}else{
      			this.bordershadow="colorRed"
      			setTimeout(()=>{
      					this.bordershadow=""
      			},1500)
      			this.$message({
				          message: '请选择一行',
				          duration:'2000',
				          type:'warning'
			         	});
      		}
      	},
	  	  //分页
	  	  handleCurrentChange(val){
	         this.currentPage = val;
	         this.loading=true;
	         this.getErpSpxxPageList();
        },
        filterhasimage(value, row, column) {
	         return row["hasimage"] == value;
	      },
	      filterauditstatus(value, row, column) {
	         return row["auditstatus"] == value;
	      },
	      uploadUrl() {
	        let url = "http://localhost:8080/product/uploadSpImageBySpid/"+ this.spid
	        return url
    		},
	      beforeUpload(file) {
			   this.showNoticeUploading = true
		          const isLt2M = file.size / 1024 / 1024 < 2;
		          if (!isLt2M) {
		            this.$message.error('上传头像图片大小不能超过 2MB!')
		            return
		          }   
      	},
	      // 确认添加
	      addEnsure(){
	        if(!this.uploadComplete){
	          this.$message.error("图片正在上传，请稍等");
	          return;
	        }
	        this.$refs.addForm.validate((valid) => {
	          if(valid){
	            let params = {
	              photo: this.addForm.photo,
	            };
	           console.info(params);
	            // 调用接口...
	            
	          } else {
	            this.$message.error("请填写所有必填项");
	          }
	        });
	      }, 
	     	// 上传图片成功
	      uploadSuccess(res, file) {
	        if (this.activeName == '1') {
	          this.imageUrl = URL.createObjectURL(file.raw)
	          this.showNoticeUploading = false
	          this.noticeImageUrl = res
	          this.noteInform.templatePicture = res
	        } else if (this.activeName == '3') {
	          this.markingpic = URL.createObjectURL(file.raw)
	          this.showMarkingUploading = false
	          this.marketImageUrl = res
	          this.marketingInform.templatePicture = res
	        }
	      },
	      // 上传图片出错
	      uploadError() {
        	this.$message.error('上传失败，请重新上传')
      	},
	       // 移除图片
	      handleRemove(file, fileList) {
	        //this.fileChange(fileList);
	      },
	      handleFinshUpload(){
	      	this.getErpSpxxPageList();
	      	this.dialogVisible=false;
	      },
	      handleFinshSee(){
	      	this.getErpSpxxPageList();
	      	this.dialogVisibleSee=false;
	      },
	      //浏览图片
	      seeImage(data){
	      	this.dialogVisibleSee=true;
	      		this.$api.image('product/getErpSpImagePageList/'+data,  {
      		},localStorage.token, success => {
	         	this.cardImgs=success;
					  },
					 failure=>{
					 	
					 })
	      },
	      zoomInImage(data){
	      	this.dialogVisibleCard=true;
	      	this.dialogImageUrlCard=data;
	      },
	      //删除图片
	      deleteImage(id,index){
	      	 this.$confirm('确定删除当前图片?', 'Warning', {
		          confirmButtonText: '确定',
		          cancelButtonText: '取消',
		          type: 'warning'
		       }).then(() => {
			      		this.$api.delete('product/removeSpImageBySpimageid/'+id,  {
	      		},localStorage.token, success => {
		         	this.cardImgs.splice(index,1);
		         	this.$message({
			          message: success.msg,
			          type: 'success'
			        });
						  },
						 failure=>{
						 	
						 })
	        }).catch(() => {
	               
	        });
	      }
      }
		}


