import Vue from 'vue'
import Router from 'vue-router'
import sptb from '@/components/nav/sptb/sptb'
import zzbao from '@/components/nav/zzbao/zzbao'



Vue.use(Router)

export default new Router({
  //mode:"history",
  routes: [
    {
      path: '/',
      name: 'login',
      redirect: '/login',
      component: resolve=>require(['@/components/login'],resolve)
    },
    {
      path: '/login',
      name: 'login',
      component: resolve=>require(['@/components/login'],resolve)
    },
    {
      path: '/main',
      name: 'main',
      component: resolve=>require(['@/components/main'],resolve),
      redirect:'/nav/sptb/sptb',
      meta: { title: '主菜单', icon: 'dashboard', noCache: true },
      children:[
      {
      	path: '/nav/sptb/sptb',
      	name: 'sptb',
      	component:sptb, 
      	meta: { title: '商品信息表', icon: 'dashboard', noCache: true },
      	children:[
      	{
      		
	      	path: '/nav/sptb/child/add',
	      	name: 'merchandisetableAdd',
	      	component: resolve=>require(['@/components/nav/sptb/child/add'],resolve),
	      	meta: { title: '新增', icon: 'dashboard', noCache: true }
      	},
      	{
	      	path: '/nav/sptb/child/update',
	      	name: 'update',
	      	component: resolve=>require(['@/components/nav/sptb/child/update'],resolve),
	      	meta: { title: '修改', icon: 'dashboard', noCache: true }
      	}
      	]
      },
      {
      	path: '/nav/zzbao/zzbao',
      	name: 'zzbao',
      	component:zzbao,
      	meta: { title: '值值宝', icon: 'dashboard', noCache: true },
      	children:[
      	{
      		path: '/nav/zzbao/child/add',
	      	name: 'add',
	      	component: resolve=>require(['@/components/nav/zzbao/child/add'],resolve),
	      	meta: { title: '开通', icon: 'dashboard', noCache: true }
	      },
	      {
      		path: '/nav/zzbao/child/update',
	      	name: 'update',
	      	component: resolve=>require(['@/components/nav/zzbao/child/update'],resolve),
	      	meta: { title: '修改', icon: 'dashboard', noCache: true }
	      },
      	]
      },
      {
	      path: '/404',
	      name: '404',
	      component: resolve=>require(['@/components/404'],resolve),
	      meta: { title: '404', icon: 'dashboard', noCache: true }
	    },
	    {
	      path: '/500',
	      name: '500',
	      component: resolve=>require(['@/components/500'],resolve),
	      meta: { title: '500', icon: 'dashboard', noCache: true }
	    }
      ]
    },
  ]
})









