<template>
	<section>		
		<template v-if="showRouterView">
			<router-view ></router-view>
		</template>
		<template v-else>
			<template  v-if="showCard">
				<div>
					<el-row style="margin-bottom: 20px;">
					  <el-card class="box-card" :body-style="{ position:'relative'}">
						  <div slot="header" class="clearfix">
						    <span style="    font-weight: bold;color: #4f76ab;">值值宝信息</span>
						    <el-button style="float: right; padding: 3px 0" type="text" @click.native.prevent="update"></el-button>
						  </div>
						  <el-row>
							  <el-col :span="4">
							  	 <i class="fa fa-credit-card" aria-hidden="true" style="font-size: 50px;color: #4f76ab;"></i>
							  	 <div  class="text item" style="font-size: 15px;font-weight: bold;color: #4f76ab;">
								       卡号：{{idnum}}
								 </div>
							  </el-col>
							  <el-col :span="4">
							  	 <i class="fa fa-money" aria-hidden="true" style="font-size: 50px;color: #4f76ab;"></i>
							  	 <div  class="text item balance" style="font-size: 15px;font-weight: bold;color: #4f76ab;">
								     余额：{{balance}}
								 </div>
							  </el-col>
							  <el-col :span="4">
							  	 <i class="fa fa-clock-o" aria-hidden="true" style="font-size: 50px;color: #4f76ab;"></i>
							  	 <div  class="text item" style="font-size: 15px;font-weight: bold;color: #4f76ab;">
								        开通时间：{{createtime}}
								 </div>
							  </el-col>
							  <el-col :span="4">
							  	<i class="fa fa-shield" aria-hidden="true" style="font-size: 50px;color: #4f76ab;"></i>
							  	<div  class="text item" style="font-size: 15px;font-weight: bold;color: #4f76ab;">
								    状态：{{status}}
								</div>
							  </el-col>
							  <el-col :span="4">
							  	<i class="fa fa-mobile-phone" aria-hidden="true" style="font-size: 50px;color: #4f76ab;"></i>
							  	<div  class="text item" style="font-size: 15px;font-weight: bold;color: #4f76ab;">
								       手机号：{{telphone}}
								    <a href="#" @click="telUpdate">修改</a>
								</div>
							  </el-col>
							  <el-col :span="4">
							  	 <i class="fa fa-key" aria-hidden="true" style="font-size: 32px;color: #4f76ab;"></i>
							  	 <div  class="text item" style="font-size: 15px;font-weight: bold;color: #4f76ab;">
								        密码：{{password}}
								  	<a  href="#"  @click="pwdUpdate">修改</a>
								</div>
							  </el-col>
						  </el-row>
						</el-card>
					</el-row>
					<el-row :gutter="20" style="display: flex">
					  <el-col class="current">
					  	<div class="grid-content bg-purple">
					  		<el-card class="box-card">
							  	<el-table
							      :data="tableData"
							      style="width: 100%">
							      <el-table-column
							        prop="date"
							        label="日期"
							        width="180">
							      </el-table-column>
							      <el-table-column
							        prop="name"
							        label="类型"
							        width="180">
							      </el-table-column>
							      <el-table-column
							        prop="address"
							        label="金额">
							      </el-table-column>
							    </el-table>
						    </el-card>
					  	</div>
					  </el-col>
					  <el-col class="cardList">
						  	<div class="grid-content bg-purple" style="height:437px">
						  	  <template v-if="hasCard">
						  	  	  <el-carousel height="200px"  :autoplay=false style="padding:20px 0;width:340px;margin: auto;">
								      <el-carousel-item v-for="(item,index) in cardList" :style="{ background: item.cardColor}" style="border-radius: 10px;">
								      	<i :class="item.logo"></i>
								        <p class="bankname">{{ item.bankname }}</p>
								        <p class="bankno">{{ item.bankno }}</p>
								        <p class="cardtypestr">{{ item.cardtypestr }}</p>
								        <i class="yinlian iconfont icon-py_yinlian"></i>
								     </el-carousel-item>
								  </el-carousel>
						  	  </template>
						  	  <template v-else>
						  	  	 <el-carousel height="200px"  :autoplay=false style="padding:20px 0;width:340px;margin: auto;;">
								      <el-carousel-item   style="border-radius: 10px;background: #ccc">
								        <p class="bankname">xx银行</p>
								        <p style="position: absolute;top: 60px;left: 60px;color: #fff;font-size: 20px;"> 暂未绑定银行卡</p>
								        <p class="bankno">xxxxxx xxxx  xxxx   xxxx</p>
								        <p class="cardtypestr">储蓄卡</p>
								        <i class="yinlian iconfont icon-py_yinlian"></i>
								     </el-carousel-item>
								  </el-carousel>
						  	  </template>
							  <el-row>
								  <el-button type="danger" @click.native.prevent="band" class="fa fa-plus" style="background: #4f76ab;border-color: #4f76ab;">添加银行卡</el-button>
								  <el-button type="danger"@click.native.prevent="bindRemove()" class="fa fa-superpowers" style="background: #4f76ab;border-color: #4f76ab;" :class="noCardBgColor">解绑银行卡</el-button>
								  <el-button type="danger" @click.native.prevent="launch()" class="fa fa-money" style="background: #4f76ab;border-color: #4f76ab;" :class="noCardBgColor">提现</el-button>
							  </el-row>
							  </div>
						  </el-col>
					</el-row>
				</div>
			</template>
			<template  v-else>
				<div class="cardNull">
					<img src="../../../assets/images/nocard.jpg" />
					<p >暂未开通银行卡</p>
					<el-button @click.native.prevent="open" type="primary" >去开通</el-button>
				</div>
			</template>
			<el-dialog title="余额提现" :visible.sync="dialogFormVisible" width="570px">
			  <el-form ref="ruleForm" :model="ruleForm" :rules="rules" label-width="120px" style="text-align: left;">
			    <el-form-item label="提现金额" prop="amount">
			      <el-input v-model="ruleForm.amount" clearable></el-input>
			    </el-form-item>
			    <el-form-item label="提现卡号" prop="cardno">
			      <el-input v-model="ruleForm.cardno" clearable></el-input>
			    </el-form-item>
			    <el-form-item label="验证码" prop="telcode">
			      <el-input v-model="ruleForm.telcode" clearable></el-input>
			      <el-button @click.native.prevent="getTelCode" type="primary" >获取</el-button>
			    </el-form-item>
			  </el-form>
			  <span slot="footer" class="dialog-footer">
			    <el-button @click="dialogFormVisible=false">Cancel</el-button>
			    <el-button type="primary" @click.native.prevent="launchWithdrawApply">Confirm</el-button>
			  </span>
			</el-dialog>
			<el-dialog title="银行卡绑定" :visible.sync="dialogBandCardVisible" width="400px">
			    <div class="dialogBandCard">
			    	<span class="text">BANK</span>
			    	<p class="cardno">{{ruleCard.card}}</p>
			    	<i class="iconfont icon-py_yinlian" aria-hidden="true"></i>
			    </div>
			    <el-form :model="ruleCard" :rules="rules" ref="ruleCard" >
					<el-form-item prop="card">
						<el-input class="cardno" v-model="ruleCard.card" clearable placeholder="请输入银行卡号"></el-input>
					</el-form-item>
				</el-form>
			    <span slot="footer" class="dialog-footer">
				    <el-button @click="dialogBandCardVisible=false">取消</el-button>
				    <el-button type="primary" @click.native.prevent="bindWalletBankCard">确定</el-button>
				</span>
			</el-dialog>
			<el-dialog title="修改手机号码" :visible.sync="dialogTelUpdateVisible" width="400px">
			    <el-form :model="ruleCard" :rules="rules" ref="ruleCard"  style="text-align: left;">
					<el-form-item prop="newtel">
						<el-input  v-model="ruleCard.newtel" clearable placeholder="请输入手机号码"></el-input>
					</el-form-item>
					<el-form-item prop="securitycode">
						<el-input  v-model="ruleCard.securitycode" clearable placeholder="请输入验证码"></el-input>
						<el-button type="primary" @click.native.prevent="updateGysWalletTelSendEmailByTel">获取</el-button>
					</el-form-item>
				</el-form>
			    <span slot="footer" class="dialog-footer">
				    <el-button @click="dialogTelUpdateVisible = false">取消</el-button>
				    <el-button type="primary" @click.native.prevent="updateGysWalletTel">确定</el-button>
				</span>
			</el-dialog>
			<el-dialog title="修改密码" :visible.sync="dialogPwdUpdateVisible" width="400px">
			    <el-form :model="ruleCard" :rules="rules" ref="ruleCard"  style="text-align: left;" >
					<el-form-item prop="oldmima">
						<el-input  v-model="ruleCard.oldmima" clearable placeholder="请输入原始密码"></el-input>
					</el-form-item>
					<el-form-item prop="mima">
						<el-input  v-model="ruleCard.mima" clearable placeholder="请输入密码"></el-input>
					</el-form-item>
					<el-form-item prop="securitycode">
						<el-input  v-model="ruleCard.securitycode" clearable placeholder="请输入验证码"></el-input>
						<el-button type="primary" @click.native.prevent="updateGysWalletPassWordSendEamilByTel">获取</el-button>
					</el-form-item>
				</el-form>
			    <span slot="footer" class="dialog-footer">
				    <el-button @click="dialogPwdUpdateVisible = false">取消</el-button>
				    <el-button type="primary" @click.native.prevent="updateGysWalletPassWord">确定</el-button>
				</span>
			</el-dialog>
		</template>
	</section>
</template>

<script>
  import zzbao from '../../../assets/js/nav/zzbao/zzbao.js'
  export default zzbao
</script>

<style scoped lang="scss" >
@import '../../../assets/css/nav/zzbao/zzbao.scss'
</style>
<style>
.el-col {
    border-radius: 4px;
}
.bg-purple {
    -webkit-box-shadow: 0 2px 12px 0 rgba(0,0,0,.1);
    box-shadow: 0 2px 12px 0 rgba(0,0,0,.1);
}
.el-table td, .el-table th {
    padding: 6px 0!important;
}
</style>


